const myButton = document.querySelector(".button");
if (myButton) {
    myButton.addEventListener("click", function () {
        switch (myButton.id) {
            case 'article-module-bundlers':
                window.location = "module-bundlers.html";
                break;
            case 'list-categories':
                window.location = "index.html";
                break;
            case 'article-preprocesador':
                window.location = "preprocesadores.html";
                break;
        }
    });
}


